import React from "react";
import useSelf, { selfCodeItem } from "../../Data/useSelfCode";
import styles from "./index.module.css";
import { Button, Space } from "antd";

const PageSelf = (props: any) => {
  const { self } = useSelf();

  return (
    <div className={styles.container}>
      <div className={styles.left}>
        <Button block type="dashed" onClick={() => props.setRouter("home")}>
          首页
        </Button>
        <div className={styles.list}>
          {self?.map((item: selfCodeItem) => {
            const { name } = item;
            return (
              <div key={name}>
                <span>{name}</span>
              </div>
            );
          })}
          <div style={{ height: "1000px" }}></div>
        </div>
      </div>
      <div className={styles.content}>
        {self?.map((item: selfCodeItem) => {
          const { name } = item;
          return (
            <div key={name}>
              <span>{name}</span>
            </div>
          );
        })}
        <div style={{ height: "1000px" }}></div>
      </div>
    </div>
  );
};

export default PageSelf;
