export type CommonApiResponse = {
  count: number;
  has_more: boolean;
  fields: string[];
  items: any[][];
} | null;

const query = async (
  api_name: string,
  params: any,
  fields: string[]
): Promise<CommonApiResponse> => {
  return fetch(`${window.location.protocol}//api.tushare.pro`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: JSON.stringify({
      token: "fe77ac557743da478707c151abbe7b8f2dbdee278c983b2c82907827",
      api_name,
      params,
      fields,
    }),
  })
    .then((response) => {
      if (!response.ok) {
        throw new Error(response.statusText);
      }
      return response.json();
    })
    .then((data) => {
      return data?.data;
    })
    .catch((error) => {
      console.error(error);
      throw error;
    });
};

const PageManage = (props: any) => {
  const siRef = useRef<any>(null);
  const [loading, setLoading] = useState(false);
  const [remanent, setRemanent] = useState(0);

  const [baseData, setBaseData] = useState({
    stock_basic: 0,
    stock_company: 0,
    industry: 0,
  });

  const init = async () => {
    const stock_basic: CommonApiResponse = await localforage.getItem(
      "stock_basic"
    );
    const stock_company: CommonApiResponse = await localforage.getItem(
      "stock_company"
    );
    const industry: CommonApiResponse = await localforage.getItem("industry");
    setBaseData({
      stock_basic: stock_basic?.items?.length || 0,
      stock_company: stock_company?.items?.length || 0,
      industry: industry?.items?.length || 0,
    });

    const ts_codes = await getRemanentTsCode();
    setRemanent(ts_codes?.length || 0);
  };

  useEffect(() => {
    init();
  }, []);

  const handleDownloadStock = async () => {
    setLoading(true);
    let res = await Promise.all([
      query("stock_basic", {}, [
        "ts_code",
        "symbol",
        "name",
        "area",
        "industry",
        "cnspell",
        "market",
        "list_date",
        "act_name",
        "act_ent_type",
        "fullname",
        "enname",
        "exchange",
        "curr_type",
        "list_status",
        "delist_date",
        "is_hs",
      ]),
      query("stock_company", {}, [
        "ts_code",
        "com_name",
        "com_id",
        "chairman",
        "manager",
        "secretary",
        "reg_capital",
        "setup_date",
        "province",
        "city",
        "introduction",
        "website",
        "email",
        "office",
        "business_scope",
        "employees",
        "main_business",
        "exchange",
        "ann_date",
      ]),
      query("index_member_all", { limit: "10000" }, [
        "l1_code",
        "l1_name",
        "l2_code",
        "l2_name",
        "l3_code",
        "l3_name",
        "ts_code",
        "name",
        "in_date",
        "out_date",
        "is_new",
      ]),
    ]);

    await Promise.all([
      localforage.setItem("stock_basic", res[0]),
      localforage.setItem("stock_company", res[1]),
      localforage.setItem("industry", res[2]),
    ]);

    setLoading(false);
    init();
  };

  const getRemanentTsCode = async () => {
    const stock_basic: CommonApiResponse = await localforage.getItem(
      "stock_basic"
    );
    const exist = await financeLocalforage.keys();
    const ts_codes = stock_basic?.items
      ?.map((item: string[]) => {
        return item[0];
      })
      .filter((tc: string) => {
        return !exist.includes(tc);
      });
    return ts_codes;
  };
  const handleDownloadFinance = async () => {
    const ts_codes = await getRemanentTsCode();

    setLoading(true);
    siRef.current = setInterval(() => {
      const ts_code = ts_codes?.shift();
      setRemanent(ts_codes?.length || 0);
      if (ts_code) {
        query(
          "fina_indicator",
          {
            ts_code,
            update_flag: "1",
          },
          financeFields
        ).then((res) => {
          if (res?.items) {
            financeLocalforage.setItem(ts_code, res?.items);
          }
        });
      } else if (siRef.current) {
        clearInterval(siRef.current);
        setLoading(false);
      }
    }, 500);
  };

  return (
    <div className={styles.container}>
      <br />
      <Card title="stock_base stock_company industry">
        <Space size="middle">
          <Button loading={loading} type="dashed" onClick={handleDownloadStock}>
            Download
          </Button>
          <span>stock_basic: {baseData.stock_basic}</span>
          <span>stock_company: {baseData.stock_company}</span>
          <span>industry: {baseData.industry}</span>
        </Space>
      </Card>

      <br />

      <Card title="Finance">
        <Space size="middle">
          <Button
            disabled={baseData.stock_basic <= 0}
            loading={loading}
            type="dashed"
            onClick={handleDownloadFinance}
          >
            Download
          </Button>

          <Button
            disabled={!siRef.current}
            type="dashed"
            onClick={() => {
              clearInterval(siRef.current);
              setLoading(false);
            }}
          >
            Stop
          </Button>

          <span>
            剩余: {remanent} / {baseData?.stock_basic}
          </span>
        </Space>
      </Card>
    </div>
  );
};

export default PageManage;
