import { useEffect, useState } from "react";
import mySelf from "../self.json";

export type selfCodeItem = {
  ts_code: string;
  name: string;
  type: string;
};

const useSelfCode = () => {
  const [self, setSelf] = useState<selfCodeItem[]>(mySelf);

  useEffect(() => {
    window.sessionStorage.setItem("self", JSON.stringify(self));
  }, [self]);

  return {
    self,
    setSelf,
  };
};

export default useSelfCode;
