import { useEffect, useState } from "react";
import qs from "qs";
import "./App.css";
import PageHome from "./Components/PageHome";
import PageSelf from "./Components/PageSelf";
import PageCompare from "./Components/PageCompare";
import PageManage from "./Components/PageManage";
import PageDetail from "./Components/PageDetail";
import PageIndustry from "./Components/PageIndustry";

import initData from "./Data/init";
initData();

const getQueryObj = () => {
  return qs.parse(window.location.search, {
    ignoreQueryPrefix: true,
  });
};

function App() {
  const [router, setRouter] = useState(getQueryObj().router);

  const setRouterFunc = (name: string, code?: string) => {
    const queryString = qs.stringify({
      router: name,
      code,
    });
    window.history.pushState(
      {},
      document.title,
      window.location.pathname + `?${queryString}`
    );
    setRouter(name);
  };

  useEffect(() => {
    const goto = () => {
      setRouter(getQueryObj().router);
    };
    window.addEventListener("popstate", goto);
    return () => {
      window.removeEventListener("popstate", goto);
    };
  }, []);

  return (
    <>
      {!router || router === "home" ? (
        <PageHome setRouter={(name: string) => setRouterFunc(name)} />
      ) : null}
      {router === "self" ? (
        <PageSelf setRouter={(name: string) => setRouterFunc(name)} />
      ) : null}
      {router === "compare" ? (
        <PageCompare setRouter={(name: string) => setRouterFunc(name)} />
      ) : null}
      {router === "manage" ? (
        <PageManage setRouter={(name: string) => setRouterFunc(name)} />
      ) : null}
      {router === "detail" ? (
        <PageDetail setRouter={(name: string) => setRouterFunc(name)} />
      ) : null}
      {router === "industry" ? (
        <PageIndustry setRouter={(name: string) => setRouterFunc(name)} />
      ) : null}
    </>
  );
}

export default App;
